#portfolio-page {
  max-width: none;
}

.portfolio-card {
  height: 24vw;
  max-height: 400px;
  margin: 1vw 0px;
}

.portfolio-card .card-title h1 {
  font-size: min(7vw, 84px);
}

.portfolio-card .card-title:not(:hover) {
  opacity: 0.75 !important;
}

.portfolio-container {
  margin-bottom: 25px;
}

@media (min-aspect-ratio: 4/3) {
  .portfolio-container {
    display: flex;
    justify-content: space-between;
  }

  .portfolio-container a {
    flex-basis: 32%;
  }

  .portfolio-card {
    min-height: 75vh;
    margin: 0;
  }

  .portfolio-card .card-title h1 {
    font-size: min(5vw, 84px);
  }
}

@media (max-width: 900px) {
  .portfolio-card {
    height: 20vh;
    max-height: 250px;
    margin: 10px 0px;
  }
}

@media (max-width: 600px) {
  .portfolio-card {
    height: 16vh;
    max-height: 160px;
    margin: 10px 0px;
  }

  .portfolio-card .card-title h1 {
    font-size: 36px;
  }
}
